import Helper from '@/function.js';
export default {
	data() {
		return {
			token: '',
			info: {},
			item: {},
			imgPath: "",
			imgSvr: "",

		}

	},
	onLoad: function(option) {

		this.imgSvr = this.ImgServer;
		this.token = uni.getStorageSync('token');
		var orderNo = option.orderNo;
		//console.log(option);
		this.info.price=0;
		this.info.quota=0;
		this.getTask(orderNo);

	},

	onShow: function() {



	},

	methods: {
		getTask(orderNo) {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})

			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetMyTaskDetails?orderNo=' + orderNo,
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					if (Helper.CheckData(res)) {
						var item = res.data.data;
						_this.imgPath = item.taskPic;
						_this.info = item;
						//console.log(item);
					} else {
						uni.showModal({
							title: "信息提示",
							content: res.data.errorMessage,
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.switchTab({
										url: "/pages/mytask/mytask"
									})
								}
							}

						})

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		onCopyResult(type) {
			if (type === 'success') {
				uni.showToast({
					title: "复制成功",
					icon: "none"
				})
			} else {
				console.log(2)
			}
		},
		sendTask() {
			var _this = this;


			if (Helper.isEmpty(_this.imgPath)) {
				uni.showToast({
					title: "请上传图片",
					icon: "none"
				});
				return;
			}

			var params = {
				taskPic: _this.imgPath,
				orderNo: _this.info.orderNo
			}

			uni.showLoading({
				title: "请稍候..."
			})


			uni.request({
				url: _this.ApiGateWay + '/sale/Member/SubmitTask',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					if (Helper.CheckData(res)) {
						uni.showModal({
							title: "信息提示",
							content: "提交成功,请等待审核!",
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.switchTab({
										url: "/pages/mytask/mytask"
									})
								}
							}
						})
					} else {
						uni.showModal({
							title: "信息提示",
							content: res.data.errorMessage,
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.switchTab({
										url: "/pages/mytask/mytask"
									})
								}
							}

						})

					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}


			})
		},
		openUrl() {
			var url = navigator.userAgent;
			if (url.indexOf("cdridge-android") > 0) {
				wv.goUrl(this.info.url)
			} else {
				location.href = this.info.url;
			}


		},
		copyData() {
			var _this = this;
			uni.setClipboardData({
				data: _this.info.url,
				success: function() {
					uni.showToast({
						title: "复制成功",
						icon: "none"
					})
				}
			});
		},
		selectPic1() {
			var _imgPath = '';
			var _this = this;
			uni.chooseImage({
				count: 6, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: function(res) {
					//console.log(JSON.stringify(res.tempFilePaths));
					let _size = res.tempFiles[0].size;

					uni.getImageInfo({
						src: res.tempFilePaths[0],
						success: function(image) {
							_this.upload(image.path);
						}
					});
				}
			});
		},

		selectPic() {
			var _this = this
			var imgPath = "";

			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function(res) {

					imgPath = res.tempFilePaths.toString()
					uni.getImageInfo({
						src: imgPath,
						success: function(image) {
							let canvasWidth = image.width //图片原始长宽
							let canvasHeight = image.height;
							let base = canvasWidth / canvasHeight;
							//设置画布最大宽度
							if (canvasWidth > 300) {
								canvasWidth = 300;
								canvasHeight = Math.floor(canvasWidth / base);
							}
							let img = new Image();
							img.src = imgPath; // 要压缩的图片  
							let canvas = document.createElement('canvas');
							let ctx = canvas.getContext('2d');
							canvas.width = canvasWidth;
							canvas.height = canvasHeight;
							// 清除画布
							ctx.clearRect(0, 0, canvasWidth, canvasHeight);
							//  将图片画到canvas上面   使用Canvas压缩  
							ctx.drawImage(img, 0, 0, canvasWidth, canvasHeight);
							// canvas.toDataURL 返回的是一串Base64编码的URL
							// 指定格式 PNG  
							imgPath = canvas.toDataURL("image/jpeg");
							//console.log(imgPath)
							_this.upload(imgPath);
						}
					});
				}

			});

		},

		/* 上传图片 */
		upload(imgurl) {
			//http://localhost:25630
			uni.showLoading({
				title: '请稍候...'
			});
			var _this = this;

			uni.uploadFile({
				url: _this.ApiGateWay + '/sale/image/uploadByFile', //仅为示例，非真实的接口地址
				filePath: imgurl, //上传图片的路径
				name: 'file', //对应的key
				formData: {
					path: '/sale/newtask/'
				},
				success: res => {
					uni.hideLoading();
					let _res = JSON.parse(res.data);
					if (_res.errorCode == 0) {
						uni.showToast({
							icon: 'success',
							title: '图片上传成功!'
						});

						_this.imgPath = _res.data;

						//
					} else {

						uni.showModal({
							title: '提示',
							content: '图片上传失败',
							showCancel: false,
							success: function(res) {}
						});
					}
				},
				fail() {
					uni.hideLoading();
				},
				complete() {
					_this.upload_field = '';
				}
			});
		}
	}

}
